:root {
  --ivory-mist: #FDF7FA;           /* Soft Rose Pearl - background */
  --lavender-smoke: #DAB6E0;       /* Sweet Lilac Mist - accents (buttons, links) */
  --rose-quartz: #F9C9D6;          /* Blush Petal Glow - hover effects, quotes */
  --evening-charcoal: #463B57;     /* Deep Lavender Ink - main text */
  --dusk-mauve: #C497DC;           /* Aurora Lilac Glow - subtext, borders */
  --sage-dust: #E3F0EA;            /* Minted Glow - optional contrast */
}

body {
  margin: 0;
  background-color: var(--ivory-mist);
  color: var(--evening-charcoal);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

a {
  color: var(--lavender-smoke);
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--rose-quartz);
}

/* Example subtext styling */
.subtext {
  color: var(--dusk-mauve);
}

/* Example border or divider */
.border {
  border-color: var(--dusk-mauve);
}

/* Optional subtle background or highlight */
.highlight {
  background-color: var(--sage-dust);
  padding: 0.25em 0.5em;
  border-radius: 4px;
}