:root {
  --bg: #FDF7FA;           /* Soft Rose Pearl - warmer, delicate light bg */
  --text: #463B57;         /* Deep Lavender Ink - richer, elegant text */
  --accent1: #DAB6E0;      /* Sweet Lilac Mist - dreamy pastel purple */
  --accent2: #F9C9D6;      /* Blush Petal Glow - soft glowing pink hover */
  --shadow: #F3E8F1;       /* Fairy Dust Shadow - very soft pinkish glow */
  --highlight: #E3F0EA;    /* Minted Glow - soft minty highlight tint */
}

[data-theme='dark'] {
  --bg: #3A2F57;           /* Royal Velvet Indigo - richer dark bg */
  --text: #FFF6F9;         /* Ivory Glow - warm, soft light text */
  --accent1: #C497DC;      /* Aurora Lilac Glow - richer royal lilac */
  --accent2: #F2A9BE;      /* Rose Glow Radiance - warm glowing pink */
  --shadow: #51386A;       /* Plum Veil Shadow - soft dark purple glow */
  --highlight: #C9E5DD;    /* Moonlight Mint Glow - bright mint highlight */
}

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--bg);
  color: var(--text);
}